.category{
    grid-area: d;
    background-color: #FFA500;
    height: 50px;
    padding: 5px;
    border-left: solid rgba(0, 0, 0, 0.623) 280px;
    box-shadow: black 0px 0px 2px 0px;
}
.category>span{
    color: #FFA500;
    font-size: 26px;
    font-family: Arial, Helvetica, sans-serif;
    padding:10px;
    margin-left: -280px;
    padding-left: 88px;
    padding-right: 88px;
    position: absolute;
    
}
.category>input{
    margin-top: 11px;
    padding: 5px;
    margin-left: 150px;
    border: #603E00 solid 2px;
}
.category>button{
    padding: 5px;
    background-color: #F9B20C;
    border: #603E00 solid 2px;
    cursor: pointer;
    font-family: Georgia, 'Times New Roman', Times, serif;
    margin-left: 4px;
}
.category>button:hover{
    background-color: #253147;
    transition: 0.5s;
    color: white;
    border: 2px solid #9F7105;
}
.korzina{
    position: relative;
    width: 200px;
    height: 50px;
    margin-left: 680px;
    margin-top: -41px;
   
}

.korzina img{
    position: absolute;
    width: 48px;
    height: 48px;
    border: #F9B20C solid 2px;
    border-radius: 100%;
    margin-left: 172px;
    margin-top: -1px;
    z-index: 9;
}

.nameKorzina{
    position: absolute;
    width: 30px;
    font-size: 22px;
    border: #EDEDED solid 1px;
    border-radius: 40px 0 0 40px;
    border-right: none;
    padding: 10px 110px 10px 10px;
    margin-left: 51px;
    color: #253147;
    display: none;
    
    
}
.korzina>:hover>.nameKorzina{
      display: block; 
      background: rgba(0, 0, 0, 0.26);
     
      
}
