.slaydphones{
    width: 100%;
    height: 432px;
    margin-top: 5px;
}
.slaydphones>img{
    width: 100%;
    height: 100%;
}
.all{
    display: inline-grid;
    background-color: #EDEDED;
    box-shadow: 0 0 2px 0 #4B4A44;
    text-align: center;
    margin-top: 20px;
    padding: 20px;
    font-size: 20px;
    
  }
  .all img{
    width: 200px;
    height: 220px;
    margin-top: 0px;
    
  }
  .items{
      margin-left: -285px;
      margin-top: -12px;
  }
  .items a{
    text-decoration: none;
    padding-bottom: 10px;
    color: rgb(95, 94, 93);
    
  }
  
  .all:hover{
    color: rgb(12, 12, 12);
    transform: scale(1.02);
    transition: 0.3s;
    
   
  }