.nav{
    grid-area: n;
    background-color: #FFA500;
    margin-right: 5px;
    height: 437px;
    width: 280px;
    
    
    
  }
  .item{
    border-bottom: #253147 solid 2px;
    box-shadow:  0 0 2px 0;
    padding: 25px;
    padding-left: 70px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 18px;
    
    
  }

  nav a{
    text-decoration: none;
    color: rgb(25, 27, 27);
  }
   
  .item:hover{
    background-color: #253147;
    
  }
  .item a:hover{
    color: cornsilk;
  }
 .fonts{
   display: grid;
   justify-content: center;
   position: absolute;
   font-size: 35px;
   margin-top: -5px;
   margin-left: -50px;
   width: 40px;
   
 }
 
