
.profile{
    grid-area: c;
    height: 100%;
     
  }
 
  .profile img{
    width: 100%;
    height: 428px;
    margin-top: 8px;
    
  }
  .brand{
    width: 1220px;
    margin-top: 6px;
    margin-left: -285px;
} 
 .logo img{
    width: 174px;
    height: 70px;
    position: relative;
    background-color: #FFFFFF;
    box-shadow: 0 0 5px 0 black;
    margin-top: 8px;
  }
  .logo img:hover{
    transform: scale(1.1);
  }
  
  .hotItems{
    background-color: #4B4A44;
    color: #FFA500;
    font-size: 24px;
    position: relative;
    text-align: center;
    margin-top: 8px;
    width: 780px;
    margin-left: -285px;
    font-family: I;
    letter-spacing: 2px;
    border-left: 220px solid #FFA500;
    border-right: 220px solid #FFA500;
    border-radius: 50px 1px 50px;
    
    
  }
  .general{
    width: 1220px;
    margin-left: -285px;
    height: 100%;
    margin-top: -8px;
    
    
  }
 
  .all{
    display: inline-grid;
    background-color: #EDEDED;
    box-shadow: 0 0 2px 0 #4B4A44;
    text-align: center;
    margin-top: 20px;
    padding: 22px;
    font-size: 20px;
    
  }
  .all img{
    width: 200px;
    height: 220px;
    margin-top: 0px;
    
  }
  .items a{
    text-decoration: none;
    padding-bottom: 10px;
    color: rgb(95, 94, 93);
    
  }
  
  .all:hover{
    color: rgb(12, 12, 12);
    transform: scale(1.02);
    transition: 0.3s;
    
   
  }
  
  